/* Start Banner */
.banner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  @include breakpoint(991) {
    height: calc(100vh - 60px);
  }
  .banner__background {
    margin-top: -131px;
    width: 100%;
    z-index: 1;
    pointer-events: none;
    @include breakpoint(2559) {
      margin-top: -121px;
    }
    @include breakpoint(2479) {
      margin-top: -111px;
    }
    @include breakpoint(2299) {
      margin-top: -101px;
    }
    @include breakpoint(2099) {
      margin-top: -91px;
    }
    @include breakpoint(1919) {
      margin-top: -81px;
    }
    @include breakpoint(1679) {
      margin-top: -71px;
    }
    @include breakpoint(1439) {
      margin-top: -61px;
    }
    @include breakpoint(1199) {
      margin-top: -51px;
    }
    @include breakpoint(991) {
      height: 100%;
      margin-top: -101px;
    }
    @include breakpoint(479) {
      margin-top: -40px;
    }
    img {
      width: 100%;
      @include breakpoint(991) {
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .banner__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 440px;
    z-index: 1;
    margin-top: -101px;
    @include breakpoint(2299) {
      margin-top: -81px;
    }
    @include breakpoint(2099) {
      margin-top: -61px;
    }
    @include breakpoint(1439) {
      margin-top: -41px;
    }
    @include breakpoint(1199) {
      margin-top: -31px;
    }
    @include breakpoint(991) {
      margin-top: -61px;
    }
    @include breakpoint(575) {
      width: 100%;
      padding: 0 15px;
    }
    @include breakpoint(479) {
      margin-top: -40px;
    }
    @include breakpoint(374) {
      margin-top: 0;
    }
    &-text {
      font-size: 32px;
      font-weight: 700;
      color: white;
      text-shadow: 2px 3px rgba(0, 0, 0, 0.5);
      text-align: center;
      @include breakpoint(1439) {
        font-size: 30px;
      }
      @include breakpoint(1199) {
        font-size: 28px;
      }
      @include breakpoint(991) {
        font-size: 26px;
      }
      @include breakpoint(767) {
        font-size: 24px;
      }
      @include breakpoint(575) {
        font-size: 22px;
      }
    }
    &-button {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 15px;
      @include breakpoint(374) {
        flex-direction: column;
      }
      a {
        display: block;
        margin: 0 5px;
        text-align: center;
        &:last-child {
          @include breakpoint(374) {
            margin-top: 5px;
          }
        }
        img {
          max-width: unset;
          width: 250px;
          @include breakpoint(1919) {
            width: 200px;
          }
          @include breakpoint(1439) {
            width: 175px;
          }
          @include breakpoint(991) {
            width: 150px;
          }
        }
        &:hover {
          transform: translateY(-5px);
        }
      }
    }
  }
  // &::after {
